var getRmb=require('../../common/requset.js')
import keyboardPackage from '../../components/keyboard-package/keyboard-package.vue';
import passwordInput from '../../components/password-input/password-input.vue';
export default {
    data(){
        return {
            taste:'',
            type1:'卖出',
            state:'',
            boe:'',
            price:'',
            x:'',
            rmb:'',
            numberList: [],
            length: 6,
            type: 'number',
            passIn: false,
            frte:true,
            checkall:'展示完整信息'
        }
    },
    components: {
    	keyboardPackage,
    	passwordInput
    },
    onLoad(option) {
        var that = this
        //获取上个页面携带的数据，并复制
        var vn = JSON.parse(option.mvp)
        that.vn = vn
        var boe = that.vn[1]
        that.boe = that.vn[1]
        that.state = vn[0][0].order_status
        if(that.state == 102) {
            that.state = '已确认'
        }
        that.taste = that.vn[0][0]
        that.x = that.taste.order_num
        that.price = that.taste.sale_money
        that.rmb = getRmb.getrmb(that.price)
    },
    methods:{
        clo: function() {
        	this.passIn = false;
        	this.$refs['number'].close();
        	this.numberList.length= 0;
        },
        onDelete() {
        	this.numberList.pop();
        },
        onChange(e){
        	if(e.show==false){
        		this.passIn = false
        	}
            this.numberList.length = 0
        },
        onConfirm() {
            if(this.numberList.length!=6){
              uni.showToast({
                title: '请输入六位交易密码！',
                duration: 2000,
                icon: 'none'
              });
            }
          },
        onInput(val) {
            var that = this
        	that.numberList.push(val);
        	that.password = that.numberList.join().replace(/,/g, '');
        	if (that.numberList.length >= that.length) {
        		uni.request({
        			url: that.url + 'saleaffirm/',
        			method: 'POST',
        			data: {
                        order_num:that.x,
        				password: that.password
        			},
        			header: {
        				Authorization: 'JWT' + ' ' + this.global_.token
        			},
        			success(res) {
                        if(res.statusCode==400){
                            that.numberList.pop();
                            that.numberList.length= 0;
                            that.$refs.wrong.flag=false;
                            var n=res.data.data.err_num;
                            var s=5-n;
                            that.$refs.wrong.tip='剩余'+ s +'次机会';
                        }
                        if(res.statusCode==423){
                        	uni.showToast({
                        		title:'交易密码已锁定,请在今日24:00后进行交易',
                        		icon:'none',
                        		duration:2000
                        	})
                        }
                        if(res.statusCode==201){
                        	that.success=true
                        }
        				if (res.statusCode == 200) {
        					uni.showToast({
        						title: '收款完成',
        						icon: 'none',
        						duration: 2000
        					});
                            uni.navigateBack({
                                url:'../sale/sale'
                            })
        				}
        				var page = getCurrentPages().pop();
        				if (page == undefined || page == null) return;
        				page.onLoad(that.val);
        			},
        		});
                this.numberList.length= 0;
        	}
        },
        btn:function () {
            var that = this
            this.passIn = true;
            this.$refs['number'].open();
            this.onInput(val);
        },
        btn1:function () {
            var that = this
            that.frte=!that.frte;
            if(that.frte==false){
                that.checkall='收起';
            }else{
                that.checkall='展示完整信息'
            }
        }
    }
}
